.overview {
  padding: 60px 0;
  background: #f7f7f7;
}

.overview h3 {
  font-size: 32px;
  color: #111;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  position: relative;
  padding-bottom: 15px;
}

.overview h3::before {
  content: "";
  position: absolute;
  display: block;
  width: 120px;
  height: 1px;
  background: #ddd;
  bottom: 1px;
  left: calc(50% - 60px);
}

.overview h3::after {
  content: "";
  position: absolute;
  display: block;
  width: 40px;
  height: 3px;
  background: #18d26e;
  bottom: 0;
  left: calc(50% - 20px);
}

.overview header p {
  text-align: center;
  padding-bottom: 30px;
  color: #333;
}
.preview-item {
  text-decoration: none;
  color: #333;
  margin-bottom: 3rem;
}

.preview-item:hover {
  text-decoration: none;
  color: #18d26e;
}

.preview-item figure {
  margin: 0;
  border-bottom: 1px solid #000;
}
.preview-item .info {
  background: #fff;
  text-align: center;
  padding: 30px 15px 30px 15px;
  height: 90px;
  border-radius: 0 0 3px 3px;
}

.preview-item h4 {
  font-size: 18px;
  line-height: 1px;
  font-weight: 700;
  margin-bottom: 18px;
  padding-bottom: 0;
}

.preview-item p {
  padding: 0;
  margin: 0;
  color: #b8b8b8;
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
}
